<template>
        <div class="loginMain" @click="bindClosePhonePrefix">
                <div class="content">
                        <div class="loginCel">
                                <p class="title">
                                        {{ t('Login.title2')
                                        }}<img class="back" src="@/assets/images/icons/back.png"
                                                @click.stop="bindPageBack" />
                                </p>
                                <div class="taps_wrap">
                                        <p class="tap" :class="{ active: registerType === 1 }"
                                                @click="changeLoginType(1)">
                                                {{ t('Login.registerType1') }}
                                        </p>
                                        <p class="tap" :class="{ active: registerType === 2 }"
                                                @click="changeLoginType(2)">
                                                {{ t('Login.registerType2') }}
                                        </p>
                                </div>
                                <template v-if="registerType === 1">
                                        <registerEmail></registerEmail>
                                </template>
                                <template v-else>
                                        <registerPhone ref="registerPhoneRef"></registerPhone>
                                </template>

                                <p class="goSignUp">
                                        {{ t('Login.HasAccount') }}
                                        <nuxt-link-locale to="/login" class="link">{{
                                                t('Login.title1')
                                        }}</nuxt-link-locale>
                                </p>
                                <div class="mob_line mob_show">
                                        <div class="fl">
                                                <span>{{ t('Login.HasAccount') }}
                                                        <button @click="
                                                                navigateToFn(
                                                                        '/login',
                                                                        '/pages/login/index',
                                                                )
                                                                " class="link">
                                                                {{ t('Login.title1') }}
                                                        </button></span>
                                        </div>
                                </div>
                                <div class="tips">
                                        {{ t('Login.loginTips1') }}
                                        <nuxt-link-locale to="/privacy" class="link">{{
                                                t('Login.tipsPrivacy')
                                        }}</nuxt-link-locale>,
                                        <nuxt-link-locale to="/service" class="link">{{
                                                t('Login.tipsAgreement')
                                        }}</nuxt-link-locale>.
                                        {{ t('Login.loginTips2') }}
                                </div>
                        </div>
                        <div class="mob_more mob_show">
                                <!-- <p class="title">{{ t('Login.more') }}</p>
				<div class="methods">
					<div class="items"><img class="icon" src="@/assets/images/login-google-mob.png" /></div>
				</div> -->
                                <div class="tips">
                                        {{ t('Login.loginTips1') }}
                                        <nuxt-link-locale to="/privacy" class="link">{{
                                                t('Login.tipsPrivacy')
                                        }}</nuxt-link-locale>,
                                        <nuxt-link-locale to="/service" class="link">{{
                                                t('Login.tipsAgreement')
                                        }}</nuxt-link-locale>.
                                        {{ t('Login.loginTips2') }}
                                </div>
                        </div>
                </div>
        </div>
</template>
<script setup>
import registerEmail from "./components/email.vue";
import registerPhone from "./components/phone.vue";
import { ref } from 'vue'
import { ga4 } from '@/utils/ga4'
import { nuxtTo } from '@/utils/tools'
const registerPhoneRef = ref()
const registerType = ref(1)
const { t } = useI18n()
definePageMeta({
        layout: 'header',
})
const changeLoginType = (val) => {
        registerType.value = val
}
const route = useRoute()
const source = route.query.source
const historyUrl = ref()
const bindPageBack = () => {
        const router = useRouter()
        if (source === 'app') {
                uni.navigateBack()
        } else if (!historyUrl.value) {
                nuxtTo('/')
        } else {
                router.back()
        }
}

const bindClosePhonePrefix = () => {
        if (registerPhoneRef.value) {
                registerPhoneRef.value.bindPhonePrefixTransfer()
        }
}

const navigateToFn = (url, appUrl) => {
        if (source === 'app') {
                uni.navigateTo({ url: appUrl })
        } else {
                nuxtTo(url)
        }
}

onMounted(() => {
        ga4({
                types: 'first_visit',
                params: {
                        page_name: 'Sign Up',
                },
                isDailyVisit: true,
        })
        let script = null
        script = document.createElement('script')
        script.src = '/uni.webview.1.5.1.js'
        script.type = 'text/javascript'
        document.head.appendChild(script)
})

defineExpose({ historyUrl })
</script>
<script>
import { defineComponent } from "vue";
export default defineComponent({
        beforeRouteEnter(to, from, next) {
                next(function (vm) {
                        // 判斷是不是直接從瀏覽器進入註冊頁
                        vm.historyUrl = from.name
                });
        }
});
</script>

<style scoped lang="scss" src="@/pages/login/components/index.scss"></style>
